import React from 'react';
import { Link } from 'react-router-dom';
import classes from './Home.module.scss';

const home = () => {
    return (
        <section className={classes.Home}>
            <div className={classes.Container}>
                <div className={classes.Headers}>
                    <h1>WELCOME to <span>V8P&nbsp;LONDON</span></h1>
                    <h2>We'll Make Your Car Awesome !!!</h2>
                </div>
                <div className={classes.Content}>
                    <div className={classes.Text}>
                        <ul>
                            <li>V8P LONDON is a modern automotive company based in South London (Croydon, 13-15 Stafford Road CR0 4NG).</li>
                            <li>We specialize in the field of cars and vans bodywork repairs and customs.</li>
                            <li>Our experience and professionalism guarantee our clients full satisfaction and high quality of our services.</li>
                            <li>We use the most modern methods to ensure the most accurate and quickest possible bodywork and painting work.</li>
                            <li>We offer accident repairs, body reconstruction, renovation, removal of dents and scraches, correction of the paint coating and visual tuning - customs.</li>
                            <li>Each of them is made with the highest professionalism and diligence.</li>
                            <li>We always provide our services on time and as quickly as possible. We want our customers to be able to enjoy them car's as soon as possible.</li>
                            <li>The offers of V8P LONDON also includes the rental of replacement cars for the duration of renovation if necessary.</li>
                        </ul>
                    </div>
                    <div className={classes.LogoWrapper}>
                        <div className={classes.Logo}></div>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default home;