import React from 'react';
import classes from './AboutUs.module.scss';

const aboutUs = () => {
    return (
        <section className={classes.AboutUs}>
            <ul>
                <li>V8P LONDON is a Polish company that from the very beginning has been building a leading position in providing a comprehensive offer for our clients in the automotive industry.</li>
                <li>From the very beginning, we try to be as close to our clients as possible. Knowledge of the needs and expectations in relation to the services we provide inspires us to search for new innovative solutions and technologies.</li>
                <li>We are constantly training and we will introduce new services.</li>
                <li>It is worth knowing that all the services from our offer, which we offer over the years of experience, have undergone strict tests. That is why we have developed our own and unique system of performing individual stages of work.</li>
            </ul>
        </section>
    );
};

export default aboutUs;